			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 page.num=1, 再触发upCallback方法 )
			this.mescroll.resetUpScroll()
		},
		/*上拉加载的回调: 其中page.num:当前页 从1开始, page.size:每页数据条数,默认10 */
		upCallback(page) {
			console.log(page)
			//联网加载数据
			var keyword = this.tabs[this.i]
			// console.log(page.size, this.i, keyword, page.num)
			var that = this
			// if(res=){
			var dataList = []
			api.orderList({
				status: keyword,
				page: page.num,
				size: 4,

			}, function(res) {
				console.log(res, 222)
				if (res.error == '0') {
					dataList = res.data
					console.log(dataList)
					for (let i = 0; i < dataList.length; i++) {
						// console.log(i)

						dataList[i].dateline = that.getLocalTime(Number(dataList[i].dateline) * 1000)
					}
					setTimeout(() => {
						if (page.num == 1) that.goods = []; //如果是第一页需手动制空列表

						that.goods = that.goods.concat(dataList)
						that.mescroll.endSuccess(res.data);
					}, 1000)


				} else {
					that.mescroll.endErr();
				}
			})


		},
